<template>
  <v-app>
    <v-navigation-drawer v-model="leftDrawer" app stateless>
      <v-layout row wrap class="mt-6 ma-0 pa-2">
        <!-- <v-flex xs12 class="mb-4">
          <h1 style="color: #a24187">Filters</h1>
        </v-flex> -->

        <v-flex xs12>
          <v-flex xs12 class="mb-1">
            <h4 style="color: #a24187">Select Theme</h4>
          </v-flex>
          <v-autocomplete
            outlined
            dense
            :items="themeList"
            item-text="name"
            item-value="id"
            v-model="themeId"
            :label="`${themecounter} Available`"
            auto-select-first
            @change="getInidicatorList()"
          ></v-autocomplete>
          <v-flex xs12 class="mb-1">
            <h4 style="color: #a24187">Select Indicator</h4>
          </v-flex>

          <v-autocomplete
            outlined
            return-object
            dense
            :items="indicatorsList"
            item-text="data_requirement"
            v-model="indicatorId"
            :label="`${indiCounter} Available`"
            @change="getIndicatorData()"
          ></v-autocomplete>
          <v-flex xs12 class="mb-1">
            <h4 style="color: #a24187">Select Source</h4>
          </v-flex>

          <v-autocomplete
            outlined
            dense
            :items="sourceListDrop"
            v-model="selectedSource"
            :label="`${sourceCounter} Available`"
            @change="getYear()"
          ></v-autocomplete>
          <v-flex xs12 class="mb-1">
            <h4 style="color: #a24187">Select Year</h4>
          </v-flex>

          <v-select
            outlined
            dense
            :items="yearListDrop"
            v-model="selectedYear"
            :label="`${yearCounter} Available`"
            @change="filterChild()"
          ></v-select>

          <!-- <v-autocomplete
            v-if="districtData.length > 0"
            outlined
            dense
            return-object
            :items="districtDrop"
            item-text="district"
            v-model="selectedDistrict"
            :label="`District - (${districtCounter}) Available`"
            @change="distDetails(selectedDistrict)"
          >
          </v-autocomplete> -->
          <v-radio-group v-model="radios" v-show="showradiobuttons">
            <v-flex xs12 class="mb-1">
              <h5 style="color: #a24187">
                Select Administrative Level to View Data Table or Charts
              </h5>
            </v-flex>
            <v-radio
              color="#a24187"
              v-show="districtCounter > 0"
              :label="`District - (${districtCounter}) Available`"
              value="district"
              @click="distDetails()"
            ></v-radio>
            <v-radio
              color="#a24187"
              v-show="divisionCounter > 0"
              :label="`Divisions - (${divisionCounter}) Available`"
              value="division"
              @click="divisionDetails()"
            ></v-radio>
            <v-radio
              color="#a24187"
              v-show="provinceCounter > 0"
              :label="`Province - (${provinceCounter}) Available`"
              value="province"
              @click="provinceDetails()"
            ></v-radio>

            <v-radio
              color="#a24187"
              v-show="nationalData.length > 0"
              label="National"
              value="national"
              @click="nationalDetails()"
            ></v-radio>
          </v-radio-group>

          <!-- <v-autocomplete
            v-if="divisionData.length > 0"
            outlined
            dense
            return-object
            :items="divisionData"
            item-text="division"
            v-model="selectedDivision"
            :label="`Divisions - (${divisionCounter}) Available`"
            item-value="divListCleaned"
            @change="divisionDetails(selectedDivision)"
          >
          </v-autocomplete>

          <v-autocomplete
            v-if="provinceData.length > 0"
            outlined
            dense
            return-object
            :items="provinceData"
            item-text="province"
            v-model="selectedProvince"
            :label="`Province - (${provinceCounter}) Available`"
            item-value="pHeadIndicator"
            @change="provinceDetails(selectedProvince)"
          >
          </v-autocomplete> -->

          <!-- <v-select
            outlined
            dense
            :items="surveyLevels"
            v-model="selectedSurveyLevel"
            label="Select Survey Level"
            @change="mountSHP(selectedSurveyLevel)"
          ></v-select> -->

          <!-- <v-autocomplete
            v-if="selectedSurveyLevel === 'Provincial'"
            outlined
            dense
            return-object
            :items="pHeadIndicator"
            item-text="province_name"
            v-model="selectedHeadInd"
            label="Select Province"
            item-value="pHeadIndicator"
            @change="getDiv()"
          >
          </v-autocomplete> -->

          <!-- <v-autocomplete
            v-if="divListCleaned.length > 0"
            outlined
            dense
            return-object
            :items="divListCleaned"
            item-text="division"
            v-model="selectedDiv"
            label="Select Division"
            item-value="divListCleaned"
            @change="getDist()"
          >
          </v-autocomplete> -->

          <!-- <v-autocomplete
            v-if="selectedDiv !== ''"
            outlined
            dense
            return-object
            :items="distList"
            item-text="district"
            v-model="selectedDist"
            label="Select District"
            item-value="distList"
            @change="getSpecDist(selectedDist)"
          >
          </v-autocomplete> -->
        </v-flex>
        <v-flex xs12>
          <v-divider class="my-0"></v-divider>
          <v-layout row align-center justify-space-between class="mt-2 mx-1">
            <img src="img/logos/ncsw-logo.jpg" height="44px" class="mx-1" />
            <v-spacer></v-spacer>
            <img src="img/logos/un_logo.jpg" height="44px" class="mx-1" />
          </v-layout>
          <v-layout row align-center class="ma-0">
            <v-card-text>
              <p>
                ©️ 2021- Developed by NUST.
                <br />
                All Rights Reserved.
              </p>
            </v-card-text>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-navigation-drawer>
    <v-main>
      <v-card tile height="92.6%">
        <!-- color="#a24187" -->
        <v-toolbar flat dense>
          <v-app-bar-nav-icon
            color="#a24187"
            @click="leftDrawer = !leftDrawer"
          ></v-app-bar-nav-icon>
          <v-spacer></v-spacer>

          <v-layout row align-center justify-space-between class="ma-0">
            <!-- <img src="img/logos/ncsw-logo.jpg" height="44px" class="mx-1" /> -->
            <v-spacer></v-spacer>
            <h1 style="color: #a24187">NATIONAL GENDER DATA PORTAL</h1>
            <v-spacer></v-spacer>
            <!-- <img src="img/logos/un_logo.jpg" height="44px" class="mx-1"  /> -->
          </v-layout>
          <v-spacer></v-spacer>

          <v-btn color="#a24187" v-if="rightDrawer" icon @click="rightDrawer = false">
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <div style="background-color: #a24187; height: 100%">
          <!-- <v-toolbar flat>
            <v-layout row align-center justify-space-between class="ma-0">
              <img src="img/logos/ncsw-logo.jpg" height="48px" class="mx-1" />
              <v-spacer></v-spacer>
              <img src="img/logos/UN-Women-Logo.jpg" height="48px" class="mx-1" />
            </v-layout>
          </v-toolbar> -->
          <v-divider></v-divider>

          <!-- v-card-text is used to put the map in the middle horizontally and vertically -->
          <v-card-text style="height: 100%" class="pa-2">
            <l-map
              :minZoom="minZoom"
              :max-bounds="maxBounds"
              ref="indiMap"
              style="height: 100%; z-index: 0"
              :zoom="zoom"
              :center="center"
            >
              <l-tile-layer
                v-if="baseMap == 'Dark'"
                :url="baseMaps.dark.url"
                :subdomains="baseMaps.dark.subdomains"
              ></l-tile-layer>
              <l-tile-layer
                v-if="baseMap == 'Grey'"
                :url="baseMaps.gray.url"
                :subdomains="baseMaps.gray.subdomains"
              ></l-tile-layer>

              <l-control-scale position="bottomright" :imperial="false"></l-control-scale>

              <l-control position="bottomleft">
                <v-card width="150">
                  <v-expansion-panels>
                    <v-expansion-panel>
                      <v-expansion-panel-header @click="legendColor()">
                        Legend
                      </v-expansion-panel-header>
                      <v-expansion-panel-content>
                        <!-- <h4>M_CHR</h4> -->

                        <v-flex>
                          <v-row>
                            <v-col cols="3">
                              <div
                                style="
                                  height: 20px;
                                  width: 20px;
                                  background-color: #9ec2ff;
                                  align: left;
                                "
                              ></div>
                            </v-col>
                            <v-col> <b>0 - 20%</b> </v-col>
                          </v-row>
                          <v-row>
                            <v-col cols="3">
                              <div
                                style="
                                  height: 20px;
                                  width: 20px;
                                  background-color: #7792e2;
                                  align: left;
                                "
                              ></div>
                            </v-col>
                            <v-col> <b>20 - 40%</b> </v-col>
                          </v-row>
                          <v-row>
                            <v-col cols="3">
                              <div
                                style="
                                  height: 20px;
                                  width: 20px;
                                  background-color: #5162c6;
                                  align: left;
                                "
                              ></div>
                            </v-col>
                            <v-col> <b>40 - 60%</b> </v-col>
                          </v-row>
                          <v-row>
                            <v-col cols="3">
                              <div
                                style="
                                  height: 20px;
                                  width: 20px;
                                  background-color: #2a31a9;
                                  align: left;
                                "
                              ></div>
                            </v-col>
                            <v-col> <b>60 - 80%</b> </v-col>
                          </v-row>
                          <v-row>
                            <v-col cols="3">
                              <div
                                style="
                                  height: 20px;
                                  width: 20px;
                                  background-color: #03018c;
                                  align: left;
                                "
                              ></div>
                            </v-col>
                            <v-col> <b>80 - 100%</b> </v-col>
                          </v-row>
                        </v-flex>
                      </v-expansion-panel-content>
                    </v-expansion-panel>
                  </v-expansion-panels>
                </v-card>
              </l-control>

              <l-control position="topright">
                <v-card max-width="240">
                  <v-expansion-panels>
                    <v-expansion-panel>
                      <v-expansion-panel-header> Basemaps </v-expansion-panel-header>
                      <v-expansion-panel-content>
                        <v-radio-group v-model="baseMap" :mandatory="true">
                          <v-radio
                            v-for="map in ['Grey', 'Dark']"
                            :key="map"
                            :label="map"
                            :value="map"
                            color="#a24187"
                          ></v-radio>
                        </v-radio-group>
                      </v-expansion-panel-content>
                    </v-expansion-panel>
                    <!-- <v-expansion-panel>
                      <v-expansion-panel-header> Layers </v-expansion-panel-header>
                      <v-expansion-panel-content>
                        <v-checkbox
                          v-model="genderData"
                          label="Gender Data"
                          color="#a24187"
                        ></v-checkbox>
                      </v-expansion-panel-content>
                    </v-expansion-panel> -->
                  </v-expansion-panels>
                </v-card>
              </l-control>
              <div v-if="showNationaleShp == true">
                <l-geo-json
                  v-for="(feature, i) in shapefile.geojson.features"
                  :key="`geojson_${i}`"
                  :geojson="feature"
                >
                </l-geo-json>
              </div>
              <div v-if="showDistrictShp == true">
                <l-geo-json
                  ref="dist"
                  v-for="(feature, i) in shapefile.geojson.features"
                  :key="`geojson_${i}`"
                  :geojson="feature"
                  :options="tooltipDistrict(feature)"
                  :options-style="styledistrict(feature)"
                >
                  <!-- @click="featureDetailsDistrict(feature)" -->
                </l-geo-json>
              </div>
              <div v-if="showDivisionShp == true">
                <l-geo-json
                  v-for="(feature, i) in shapefile.geojson.features"
                  :key="`geojson_${i}`"
                  :geojson="feature"
                  :options="tooltipDivision(feature)"
                  :options-style="styledivision(feature)"
                >
                  <!-- @click="featureDetailsDivision(feature)" -->
                </l-geo-json>
              </div>
              <div v-if="showProvinceShp == true">
                <l-geo-json
                  v-for="(feature, i) in shapefile.geojson.features"
                  :key="`geojson_${i}`"
                  :geojson="feature"
                  :options-style="styleProvince(feature)"
                  :options="tooltipProvince(feature)"
                >
                  <!-- @click="featureDetailsProvince(feature)" -->
                </l-geo-json>
              </div>
            </l-map>
          </v-card-text>
        </div>
      </v-card>

      <v-bottom-sheet
        transition="scale-transition"
        origin="center center"
        scrollable
        v-model="bottomsheet"
        persistent
      >
        <v-card height="405px" class="overflow">
          <v-tabs color="#a24187" center-active grow v-model="tab" slider-color="#a24187">
            <v-tab>Data Table</v-tab>

            <v-tab v-show="showChart"> Charts and Graphs </v-tab>
            <v-btn @click="bottomsheet = !bottomsheet" large text color="red"
              ><v-icon large>mdi-close</v-icon></v-btn
            >
          </v-tabs>
          <v-card-text style="height: 420px">
            <v-tabs-items v-model="tab">
              <v-tab-item>
                <v-card elevation="12" outlined>
                  <v-card-title style="color: #a24187">
                    Disaggregated Values
                    <v-spacer></v-spacer>
                    <v-text-field
                      class="shrink"
                      v-model="search"
                      append-icon="mdi-magnify"
                      label="Search"
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-card-title>
                  <v-data-table
                    item-key="id"
                    dense
                    :headers="getHeaders()"
                    :items="tableData"
                    :items-per-page="5"
                    class="elevation-1"
                    :search="search"
                    multi-sort
                  >
                    <template v-slot:[`item.current_value`]="{ item }">
                      <v-chip color="#a24187" dark>
                        {{ item.current_value }}
                      </v-chip>
                      <!-- {{ item }} -->
                    </template>
                    <template v-slot:[`item.sex`]="{ item }">
                      <td v-if="item.sex == ''">-</td>
                      <td v-else>{{ item.sex }}</td>
                    </template>
                    <template v-slot:[`item.residence`]="{ item }">
                      <td v-if="item.residence == ''">-</td>
                      <td v-else>{{ item.residence }}</td>
                    </template>
                    <template v-slot:[`item.age_group`]="{ item }">
                      <td v-if="item.age_group == ''">-</td>
                      <td v-else>{{ item.age_group }}</td>
                    </template>
                    <template v-slot:[`item.district`]="{ item }">
                      <td v-if="item.district == ''">-</td>
                      <td v-else>{{ item.district }}</td>
                    </template>
                    <template v-slot:[`item.division`]="{ item }">
                      <td v-if="item.division == ''">-</td>
                      <td v-else>{{ item.division }}</td>
                    </template>
                    <template v-slot:[`item.province`]="{ item }">
                      <td v-if="item.province == ''">-</td>
                      <td v-else>{{ item.province }}</td>
                    </template>
                    <template v-slot:[`item.specific_description1`]="{ item }">
                      <td v-if="item.specific_description1 == ''">-</td>
                      <td v-else>{{ item.specific_description1 }}</td>
                    </template>
                    <template v-slot:[`item.specific_description2`]="{ item }">
                      <td v-if="item.specific_description2 == ''">-</td>
                      <td v-else>{{ item.specific_description2 }}</td>
                    </template>
                    <template v-slot:[`item.specific_description3`]="{ item }">
                      <td v-if="item.specific_description3 == ''">-</td>
                      <td v-else>{{ item.specific_description3 }}</td>
                    </template>
                    <template v-slot:[`item.specific_name1`]="{ item }">
                      <td v-if="item.specific_name1 == ''">-</td>
                      <td v-else>{{ item.specific_name1 }}</td>
                    </template>
                    <template v-slot:[`item.specific_name2`]="{ item }">
                      <td v-if="item.specific_name2 == ''">-</td>
                      <td v-else>{{ item.specific_name2 }}</td>
                    </template>
                    <template v-slot:[`item.specific_name3`]="{ item }">
                      <td v-if="item.specific_name3 == ''">-</td>
                      <td v-else>{{ item.specific_name3 }}</td>
                    </template>
                    <!-- <template v-slot:[`item.specific_description2`]="{ item }">
                      <td v-if="item.specific_description2 == ''">-</td>
                      <td v-else>{{ item.specific_description2 }}</td>
                    </template> -->
                  </v-data-table>
                  <!-- </v-card-text> -->
                </v-card>
              </v-tab-item>
              <v-tab-item v-show="showChart">
                <v-card elevation="12" outlined class="overflow">
                  <v-row no-gutters>
                    <v-col cols="4">
                      <v-card-text
                        ><h3>{{ indicatorId.data_requirement }}</h3></v-card-text
                      >
                      <v-card-text
                        ><h5>Source:</h5>
                        <p>{{ selectedSource }}</p></v-card-text
                      >
                      <v-card-text
                        ><h5>Unit:</h5>
                        <p>{{ unit }}</p></v-card-text
                      >
                      <v-card-text>
                        <v-radio-group v-model="DisagRadios" v-show="proRadio">
                          <v-radio
                            v-show="tempSex.length > 0"
                            label="Gender"
                            value="gender"
                            @change="genderChart()"
                          ></v-radio>
                          <v-radio
                            v-show="tempResi.length > 0"
                            label="Residence"
                            value="residence"
                            @change="residenceChart()"
                          ></v-radio>
                          <v-radio
                            v-show="tempTotal"
                            label="Total"
                            value="total"
                            @change="totalChart()"
                          ></v-radio>
                        </v-radio-group>
                        <v-radio-group v-model="DisagRadiosNat" v-show="natRadio">
                          <v-radio
                            v-show="tempSex.length > 0"
                            label="Gender"
                            value="gender"
                            @change="genderChartNat()"
                          ></v-radio>
                          <v-radio
                            v-show="tempResi.length > 0"
                            label="Residence"
                            value="residence"
                            @change="residenceChartNat()"
                          ></v-radio>
                          <v-radio
                            v-show="tempTotal"
                            label="Total"
                            value="total"
                            @change="totalChartNat()"
                          ></v-radio>
                        </v-radio-group>
                      </v-card-text>
                    </v-col>
                    <v-col cols="2"></v-col>
                    <v-col cols="6">
                      <v-tabs
                        center-active
                        v-model="ctab"
                        color="#a24187"
                        slider-color="#a24187"
                      >
                        <v-tab> <v-icon color="#2E93fA">mdi-poll</v-icon> Bar</v-tab>
                        <v-tab>
                          <v-icon color="#2E93fA">mdi-chart-timeline-variant</v-icon>Line
                        </v-tab>
                        <v-tab>
                          <v-icon color="#2E93fA">mdi-scatter-plot</v-icon> Scatter</v-tab
                        >
                      </v-tabs>

                      <v-tabs-items v-model="ctab">
                        <v-tab-item>
                          <apexchart
                            ref="chart"
                            width="700"
                            height="300"
                            type="bar"
                            :options="chartoptions"
                            :series="sr"
                          >
                          </apexchart>
                          <!-- <v-card-text class="ma-auto">
                <v-row class="justify-center">
                  <div class="grey--text ms-4">Click to interact with graph.</div>
                </v-row>
              </v-card-text> -->
                        </v-tab-item>

                        <v-tab-item>
                          <apexchart
                            ref="chart"
                            width="700"
                            height="260"
                            type="line"
                            :options="chartoptions"
                            :series="sr"
                          ></apexchart>
                        </v-tab-item>

                        <v-tab-item>
                          <apexchart
                            ref="chart"
                            width="700"
                            height="260"
                            type="scatter"
                            :options="chartoptions"
                            :series="sr"
                          ></apexchart>
                        </v-tab-item>
                      </v-tabs-items>
                    </v-col>
                  </v-row>
                </v-card>
              </v-tab-item>
            </v-tabs-items>
          </v-card-text>
        </v-card>
      </v-bottom-sheet>
    </v-main>
    <v-overlay :opacity="0.8" :value="loading">
      <v-progress-circular indeterminate size="100" color="#a24187">
        Loading...
      </v-progress-circular>
    </v-overlay>
  </v-app>
</template>

<script>
import chroma from "chroma-js";

import shp from "shpjs";
import Axios from "axios";
export default {
  data() {
    return {
      unit: "",
      totalValuesPro: [],
      totalValuesNat: [],
      proRadio: false,
      natRadio: false,
      DisagRadiosNat: null,
      xAxisNatSex: [],
      xAxisNatRes: [],
      tempTotal: false,
      tempSex: [],
      tempResi: [],
      DisagRadios: null,
      genderSr: [],
      residenceSr: [],
      xAxis: [],

      radioDistrict: false,
      radioDivision: false,
      radioProvince: false,
      showradiobuttons: false,
      radios: null,

      yearListDrop: [],
      selectedYear: "",
      sourceListDrop: [],
      selectedSource: "",
      loading: true,
      leftDrawer: true,
      rightDrawer: false,
      baseMaps: {
        dark: {
          url:
            "https://tiles.stadiamaps.com/tiles/alidade_smooth_dark/{z}/{x}/{y}{r}.png",
          maxZoom: 20,
        },
        gray: {
          url:
            "https://server.arcgisonline.com/ArcGIS/rest/services/Canvas/World_Light_Gray_Base/MapServer/tile/{z}/{y}/{x}",
          maxZoom: 16,
        },
      },
      minZoom: 5,
      zoom: 5,
      center: [30, 72],
      bounds: null,
      shapefile: {
        loaded: false,
        geojson: null,
      },
      showNationaleShp: false,
      showProvinceShp: false,
      showDivisionShp: false,
      showDistrictShp: false,
      baseMap: "Grey",
      themecounter: 0,
      indiCounter: 0,
      sourceCounter: 0,
      yearCounter: 0,
      levelCounter: 0,
      provinceCounter: 0,
      divisionCounter: 0,
      districtCounter: 0,
      maxBounds: L.latLngBounds([
        [37.92686760148135, 106.47949218750001],
        [21.4121622297254, 37.48535156250001],
      ]),
      themeList: [],
      themeId: "",
      indicatorsList: [],
      indicatorId: "",
      indicatorData: [],
      // districtData: [],
      year: "",
      dasy: [], // data after source and year filter
      districtData: [],
      divisionData: [],
      provinceData: [],
      nationalData: [],
      selectedDistrict: "",
      selectedDivision: "",
      selectedProvince: "",
      districtDrop: [],
      divisionDrop: [],
      provinceDrop: [],
      bottomsheet: false,
      showDisaggregation: true,
      search: "",
      tab: 0,
      ctab: 0,
      sr: [
        {
          name: "",
          data: [],
        },
      ],
      chartoptions: {
        legend: {
          showForSingleSeries: true,
          showForNullSeries: false,
          showForZeroSeries: true,
        },
