              var val = value;
              // console.log("feature1", feature1.properties.NAME_1, val);
              layer1.bindTooltip(
                `
                  <div>
                  <h3>${name}</h3>
                  <hr style:"color:red">
                    <h4 >Value: ${val}
                    </h4>
                  </div>
            `,
                { permanent: false, sticky: true }
              );
            } else {
